$color-gun-metal:    #9c9d9a;
$color-black:        #000000;
$color-shading:      #F1F1F0;

$font-size-header:  23pt;
$font-size-sub-header:  14pt;
$font-size-body:  8pt;
$font-size-table:  9pt;

@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

/**
 * Some Basics
 */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.hidden-print {
  display: none
}

@font-face {
  font-family: 'Gotham-Light';
  src: url("../webfonts/GothamHTF-Light.otf");
}

@font-face {
  font-family: 'Gotham-Book';
  src: url("../webfonts/GothamHTF-Book.otf");
}

@font-face {
  font-family: 'Gotham-Medium';
  src: url("../webfonts/GothamHTF-Medium.otf");
}



@page {
  size: A4 landscape;
  margin: 2cm;
  padding: 0;
  margin-top: 0.8in;
  margin-bottom: 0.6in;
}

@page {
    background-image: url(http://admin.eightfourtwo.com/images/eightfourtwo_black.png);
    background-size: 166px 33px;
    background-repeat: no-repeat;
    background-position: 24cm 0.7cm;
    @bottom-right {
        color:$color-gun-metal;
        content: "Page " counter(page) " of " counter(pages);
        font-family: 'Gotham-Light';
        font-size: 12px;
        padding-right: 0;
    }
    @bottom-left {
        color:$color-gun-metal;
        font-family: 'Gotham-Light';
        font-size: 12px;
        padding-left: 0;
    }
}


@page:first {
    background-size: 199px 43px;
    background-position: 22cm 1.5cm;
    @bottom-right {
        content: "";
    }
    @bottom-left {
        content: "";
    }

}

#first-page {
    color: $color-black;
    > div {
        float:left;
        padding-left: 1cm;
    }
    h2 {
        font-family: 'Gotham-Book';
        border-bottom: 0;
        color: $color-black;
        border-top: 1px solid $color-black;
    }
    h2 span {
        font-family: 'Gotham-Light';
    }
}

body {
    font-family: 'Gotham-Light';
    font-size: $font-size-body;
    line-height: 1.3;
}

a {
    color: inherit;
    text-decoration: none;
}

.break-after {
    page-break-after: always;
    .break-after:last-child {
        page-break-after: avoid;
    }
    h2 {
        margin-bottom: 16px;
    }
}

/**
 * Typography
 */

h2 {
    font-size: $font-size-header;
    color: $color-gun-metal;
    border-bottom: 1px solid $color-gun-metal;
    margin-bottom: 48px;
    margin-top: 16px;
}

h2.bottomless {
    margin-bottom: 16px;
}

h3 {
    font-size: $font-size-sub-header;
    color: $color-gun-metal;
    margin-bottom: 8px;
}
h3.bottomless {
    margin-bottom: 2px;
}

hr {
    border-bottom: 1px solid $color-gun-metal;
}

.notes-title,
legend {
    font-family: 'Gotham-Medium';
    color: $color-gun-metal;

}

.notes-wrapper {
    margin-bottom: 12px;

}

/**
 * Forms
 */

form {
    margin-bottom: 12px;
}

h3{
    margin-top: 8px;
}

.breadcrumb,
.picker,
button, .btn,
.select2,
.lookup-field-wrapper.form-group,
input[type="hidden"] {
    display: none!important;
}

.form-group {
    width: 100%;
    clear:both;
    @include clearfix;
    margin-bottom: 4px;
    label {
        width: 40%;
        color: $color-gun-metal;
        float:left;
        padding-right: 1em;
    }
    > div {
        width: 55%;
        float:left;
        * {
            display: inline;
        }
        button {
            display: none
        }
    }
    .form-control {
        appearance: none;
        border: 0;
        color: $color-black;
        background: transparent;
        font-family: 'Gotham-Light';
    }
    .help-block {
        display: none
    }
}

/**
 * Declaration
 */


.declarations h2 {
    margin-top: 0;
    margin-bottom: 8px;
 }

.declarations h3 {
    margin-bottom: 4px;
}

 .borderless {
    margin-bottom: 8px;
 }

input[type="hidden"] {
    display: none!important;
}

.two-column,
.two-columns {
    column-count: 2;
    column-gap: 1.5cm;
    padding-right: 2cm;
}

.declarations .row {
    margin-bottom: 4px;
}
.declarations  .borderless td {
    padding-left: 0;
    vertical-align: middle;
    font-size: $font-size-body;
}
.borderless {
    margin-bottom: 4px;
}
.declarations  .borderless tbody tr:nth-child(odd) {
    background-color: transparent;
}
 .declarations .form-control {
    background-color: $color-shading!important;
 }

 .declarations .col-sm-4 {
    width: 33.333333333%;
    float:left;
 }
 .declarations .col-sm-7 {
    width: 66%;
    float:left;
 }
 .declarations h2 {
    margin-top: 8px;
    margin-bottom: 8px;
 }
 .declarations .avoid-break {
      break-inside: avoid!important;
 }

 .declarations .summary-checkbox {
    display: inline-block;
    width: 20px!important;
    height: 20px!important;
 }

 .declarations .input-transparent {
    display:block!important;
    min-height: 20px!important;
}

/**
 * Tables
 */

table {
    width: 100%;
    margin-bottom: 12px;
}
table th,
table td {
    padding: 4px;
    font-size: $font-size-table;
}
table th {
    font-family: 'Gotham-Medium';
    border-bottom: 1px solid $color-gun-metal;
    text-align:left;
}
table tbody td {
    padding-bottom: 12px;
}

table tbody tr:nth-child(odd) {
    background-color: $color-shading;
}

.seperator:last-child {
  display: none
}


.audit > tbody > tr > td {
  font-size: 8px!important;
  padding: 6px!important;
}

